import React from "react";

import arrowIcon from "assets/img/icons/white/arrow-up-right.svg";

import { Container, Image, Top, DateTime, Arrow, Title } from "./elements";

const ImageCard = ({ hoverElement, imgSrc, dateTime, shortTitle }) => (
  <Container>
    <Image src={imgSrc} hoverElement={hoverElement} />
    <Top>
      <DateTime hoverElement={hoverElement}>{dateTime}</DateTime>
      <Arrow src={arrowIcon} hoverElement={hoverElement} />
    </Top>
    <Title hoverElement={hoverElement}>{shortTitle}</Title>
  </Container>
);

export default ImageCard;
