import React from "react";
import { path } from "ramda";

import colors from "styles/colors";
import { createBody } from "utils/contentful";

import Layout from "components/layout";
import LogoImageHero from "components/LogoImageHero";
import { Row, Column } from "components/Grid.tsx";

import Title from "components/events/Title";
import Sidebar from "components/events/Sidebar";
import UpcomingEvents from "components/events/UpcomingEvents";
import Bottom from "components/Bottom";

import { lifecycle, compose } from "recompose";
import { ContentOuter, ContentInner, Body, LinkButton } from "./elements";

const Event = ({
  pageContext: { contentfulData, nextThreeEvents },
  location,
  indexPage,
}) => (
  <Layout
    location={location}
    title={contentfulData.metaTitle}
    description={contentfulData.metaDescription}
    refCode={contentfulData.refCode}
    whiteContentBackground
    whiteGutterBackground
    indexPage={indexPage}
  >
    <LogoImageHero
      logoImage={path(["companyLogo", "fields", "file", "url"], contentfulData)}
      logoHeight={contentfulData.companyLogoHeight}
      logoWidth={contentfulData.companyLogoWidth}
      backgroundImage={path(
        ["heroImage", "fields", "file", "url"],
        contentfulData,
      )}
    />
    <Title shortTitle={contentfulData.shortTitle}>
      {contentfulData.longTitle}
    </Title>
    <ContentOuter>
      <Row padding={30} gutterWidth={26}>
        <Column span={1} spanMedium={0} />
        <Column span={9} spanMedium={12}>
          <ContentInner>
            <Sidebar
              eventStartDateTime={contentfulData.eventStartDateTime}
              eventEndDateTime={contentfulData.eventEndDateTime}
              eventLocationName={contentfulData.eventLocationName}
              eventLocationAddress={contentfulData.eventLocationAddress}
              googleMapsEmbedLink={contentfulData.googleMapsEmbedLink}
              industry={contentfulData.industry}
            />
            <Body>
              {createBody(contentfulData.bodyText)}
              <LinkButton
                href={contentfulData.calendlyLink}
                target="_blank"
                rel="noopener noreferrer"
              >
                Sign up
              </LinkButton>
            </Body>
          </ContentInner>
        </Column>
        <Column span={2} spanMedium={0} />
      </Row>
    </ContentOuter>
    <UpcomingEvents events={nextThreeEvents} />
    <Bottom
      title="A card to accelerate your business."
      subtitle="Signing up will not affect your credit score."
      analyticsPath={`${contentfulData.urlPath}.bottom.signup`}
      gutterBackground={colors.offWhite}
    />
  </Layout>
);

const _lifecycle = {
  async componentDidMount() {
    // social media sidebar links from addthis
    const sidebarLinks = document.createElement("script");
    sidebarLinks.src =
      "//s7.addthis.com/js/300/addthis_widget.js#pubid=ra-5d54683ce2d5c02a";
    sidebarLinks.async = true;

    // social media inline links from addthis
    const inlineLinks = document.createElement("script");
    inlineLinks.src =
      "//s7.addthis.com/js/300/addthis_widget.js#pubid=ra-5d54683ce2d5c02a";
    inlineLinks.async = true;

    document.body.appendChild(sidebarLinks);
    document.body.appendChild(inlineLinks);
  },
};

export default compose(lifecycle(_lifecycle))(Event);
