import React from "react";

import { Container, Logo } from "./elements";

const LogoImageHero = ({
  logoImage,
  logoHeight,
  logoWidth,
  backgroundImage,
}) => (
  <Container backgroundImage={backgroundImage}>
    {logoImage && (
      <Logo
        src={logoImage}
        height={logoHeight || "auto"}
        width={logoWidth || "auto"}
      />
    )}
  </Container>
);

export default LogoImageHero;
