import React from "react";

import {
  Container,
  Box,
  DateTime,
  LocationName,
  Address,
  AddressLine,
  Map,
  Wrapper,
  Label,
  Industry,
  ShareIcons,
  ShareTitle,
} from "./elements";

const FormattedDateTime = ({ eventStartDateTime, eventEndDateTime }) => {
  const start = new Date(eventStartDateTime);
  const end = new Date(eventEndDateTime);

  // Wed, Aug 14
  const dateOptions = {
    weekday: "short",
    month: "short",
    day: "numeric",
  };

  // 11:00 AM
  const timeOptions = {
    timeStyle: "short",
    hour: "numeric",
    minute: "2-digit",
  };

  let startTime = start.toLocaleTimeString("en-US", timeOptions);
  const endTime = end.toLocaleTimeString("en-US", timeOptions);

  const startIsAM = start.getHours() < 12;
  const endIsAM = end.getHours() < 12;

  // only display AM/PM for the start time if start is AM
  // and end is PM

  // EXAMPLE:
  // 11:00 AM-1:30 PM, but 9:00-10:00 AM and 3:00-4:00 PM
  if (startIsAM === endIsAM) {
    startTime = startTime.split(" ")[0];
  }

  const formattedTime = `${startTime}\u2014${endTime}`;

  return (
    <DateTime>
      {start.toLocaleDateString("en-US", dateOptions)}
      <br />
      {formattedTime}
    </DateTime>
  );
};

const Sidebar = ({
  eventStartDateTime,
  eventEndDateTime,
  eventLocationName,
  eventLocationAddress,
  googleMapsEmbedLink,
  industry,
}) => (
  <Container>
    <Box>
      <FormattedDateTime
        eventStartDateTime={eventStartDateTime}
        eventEndDateTime={eventEndDateTime}
      />
      <LocationName>{eventLocationName}</LocationName>
      <Address>
        {eventLocationAddress.split("\n").map((line, i) => (
          <AddressLine key={`address-line-${i}`}>{line}</AddressLine>
        ))}
      </Address>
      <Map>
        <iframe
          title="map"
          src={googleMapsEmbedLink}
          frameBorder="0"
          height="100%"
          width="100%"
          style={{ border: 0 }}
          allowFullScreen
        />
      </Map>
      {industry && (
        <Wrapper>
          <Label>Industry</Label>
          <Industry>{industry.join(",\n")}</Industry>
        </Wrapper>
      )}
    </Box>
    <ShareIcons>
      <ShareTitle>Share:</ShareTitle>
      <div className="addthis_inline_share_toolbox" />
    </ShareIcons>
  </Container>
);

export default Sidebar;
