import React from "react";

import { Row, Column } from "components/Grid.tsx";

import ImageCard from "components/events/UpcomingEvents/ImageCard";

import { Container, Inner, Title, List, Event, Caption } from "./elements";

const parseDateTime = (rawDateTime) => {
  const date = new Date(rawDateTime);

  // Wed, Aug 14, 2:00 PM
  const options = {
    weekday: "short",
    month: "short",
    day: "numeric",
    timeStyle: "long",
    hour: "numeric",
    minute: "2-digit",
  };

  return date.toLocaleString("en-US", options);
};

const UpcomingEvents = ({ events }) => (
  <Container>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={0} />
      <Column span={10} spanMedium={12}>
        <Inner>
          <Title>Upcoming Events</Title>
          <List>
            {events &&
              events.map(
                ({
                  fields: {
                    thumbnailImage,
                    eventStartDateTime,
                    shortTitle,
                    shortDescription,
                    urlPath,
                  },
                }) => (
                  <Event key={urlPath}>
                    <ImageCard
                      hoverElement={Event}
                      imgSrc={thumbnailImage.fields.file.url}
                      heightToWidthRatio={0.75}
                      dateTime={parseDateTime(eventStartDateTime)}
                      shortTitle={shortTitle}
                    />
                    <Caption>{shortDescription}</Caption>
                  </Event>
                ),
              )}
          </List>
        </Inner>
      </Column>
      <Column span={1} spanMedium={0} />
    </Row>
  </Container>
);

export default UpcomingEvents;
